import { useMutation } from '@apollo/client';
import React, { useState } from 'react';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import { Button } from '../../components/Form';
import Playlist from '../../components/Playlist';
import { CREATE_CERTIFICATE } from '../../graphql/mutations';
import { FETCH_USER } from '../../graphql/queries';
import { Activity, Block as BlockType, Certificate as CertificateType, Content, Course } from '../../types/Course';
import CPFModal from './CPFModal';

import './styles.css';

export interface CertificateProps {
  course: Course | null,
  onContentClick: (content: Content) => void
  onCertificateClick: () => void
  onUpdateCertificate: (certificate: CertificateType) => void
}

const Certificate: React.FC<CertificateProps> = ({ course, onContentClick, onCertificateClick, onUpdateCertificate }) => {
  const [createCertificate] = useMutation(CREATE_CERTIFICATE);
  const [fetchUser] = useMutation(FETCH_USER);

  const [generateLoading, setGenerateLoading] = useState(false);
  const [isCPFOpen, setIsCPFOpen] = useState(false);

  let isAbleToGenerate = true;

  course?.blocks.forEach((block: BlockType) => {
    block.contents.forEach((content: Content) => {
      if(content.activitiesblock) {
        let correct = 0;
        content.activitiesblock.activities.forEach((activity: Activity) => {
          if(activity.user_answer_id === activity.correct_alternative_id) correct++;
        })
        const percentage = correct/content.activitiesblock.exercises_number * 100;
        if(percentage < 60) isAbleToGenerate = false;
        return;
      }

      if(!content.watched) isAbleToGenerate = false;
    })
  })

  const continueStudy = () => {
    let lastContent: any;

    course?.blocks.forEach((block: BlockType) => {
      block.contents.map((content: Content) => {
        if(lastContent) return;
        if(!content.watched) lastContent = content;
      })
    });

    onContentClick(lastContent);
  }

  const generateCertificate = async () => {
    try {
      const certificateMutation = await createCertificate({ variables: { id: Number(course?.id) } });
      const certificate = certificateMutation.data.createCertificate;
      onUpdateCertificate(certificate);
    } catch(error) {
      setGenerateLoading(false);
      alert('Erro ao Gerar Certificado!')
    }
  }

  const onGenerateCertificateClick = async () => {
    setGenerateLoading(true);

    try {
        const responseWebAccount = await fetchUser();
        const { cpf } = responseWebAccount.data.userMutation;
        
        if(cpf) return generateCertificate();

        setIsCPFOpen(true);
    } catch(error: any) {
      setGenerateLoading(false);
      alert('Erro ao Gerar Certificado!')
    }
  }

  const onCPFUpdate = () => {
    setIsCPFOpen(false);
    generateCertificate();
  }

  if(course?.certificate) {
    const certificateDate = new Date(course?.certificate.date_created);
    return (
      <BasicScreenInner className="video certificate">
        <Block className="video-block video-block-info">
          <BlockTitle title="Certificado" />
          <p>
            Voc conseguiu o seu certificado de concluso de curso. Clique abaixo para acess-lo e para incluir ele no seu Linkedin.
          </p>
          <div>
            <a target='_blank' href={`https://storage.googleapis.com/instrue/${course?.certificate.code}.pdf`}>
              <Button name="Baixar Certificado" />
            </a>
            <a target='_blank' href={`https://www.linkedin.com/profile/add?startTask=CERTIFICATION_NAME&name=${
                    course?.certificate.course_name
                  }&organizationId=86604142&issueYear=${certificateDate.getFullYear()}&issueMonth=${
                    (certificateDate.getMonth() + 1)
                  }&certUrl=https://storage.googleapis.com/instrue/${course?.certificate.code}.pdf&certId=${course?.certificate.code}`}>
              <Button name="Compartilhar no Linkedin" />
            </a>
          </div>
        </Block>
        <Playlist className="video-playlist" blocks={course?.blocks} onContentClick={onContentClick} onCertificateClick={onCertificateClick} />
        
      </BasicScreenInner>
    );
  }

  if(!course?.certificate && !isAbleToGenerate)
    return (
      <BasicScreenInner className="video certificate">
        <Block className="video-block video-block-info certificate-block">
          <BlockTitle title="Certificado" />
          <p>
            Para conseguir o certificado, voc deve assistir todas as video aulas e acertar pelo menos 60% de todos os exerccios propostos. Ao terminar, voc ir conseguir gerar o seu certificado nessa pgina. Por meio do seu certificado, ser possvel compartilhar com outras pessoas e adicionar no Linkedin.
          </p>
          <div className="video-block-title">
            <Button name="Continuar Estudando" onClick={continueStudy} />
          </div>
        </Block>
        <Playlist className="video-playlist" blocks={course?.blocks} onContentClick={onContentClick} onCertificateClick={onCertificateClick} />
        
      </BasicScreenInner>
    );
  
  return (
    <>
      <BasicScreenInner className="video certificate">
        <Block className="video-block video-block-info certificate-block">
          <BlockTitle title="Certificado" />
          <p>
            Parabns! Voc conseguiu finalizar todas as video aulas e atividades do curso. Desse modo, voc tem acesso ao certificado. Com o certificado, ser possvel compartilhar ele com outras pessoas e adicionar no Linkedin.
          </p>
          <div className="video-block-title">
            { generateLoading ? <Button name="Gerando Certificado.." /> : <Button name="Gerar Certificado" onClick={onGenerateCertificateClick} /> }
          </div>
        </Block>
        <Playlist className="video-playlist" blocks={course?.blocks} onContentClick={onContentClick} onCertificateClick={onCertificateClick} />
      </BasicScreenInner>
      { isCPFOpen ? <CPFModal onUpdate={onCPFUpdate} /> : null }
    </>
  );
}

export default Certificate;