function uploadImgurAPI(image: any) {
    return new Promise(async (next, reject) => { 
      const body = new FormData();
      body.append('image', image);
         
      try {
        const chamada = await fetch(`https://api.imgur.com/3/image`, {
          method: 'POST',
          headers: {
            'Authorization': 'Client-ID 5eb48366d928e85'
          },
          body
        });
              
        const dados = await chamada.json();
  
        if(dados.error)
          return reject({...dados, status: chamada.status});
    
        next(dados);
      } catch(erro) {
        console.log(erro);
        reject('Erro ao Conectar ao Servidor');
      }
    });
  }

export default uploadImgurAPI;