import './styles.css';

export interface ErrorPageProps {
  text: string
}

const ErrorPage: React.FC<ErrorPageProps> = ({ text }) => {
  return (
    <div className="error-page">
      <div>
        <h2>Ateno!</h2>
        <p>{text}</p>
      </div>
    </div>
  );
}

export default ErrorPage;