import { useMutation } from '@apollo/client';
import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import ErrorPage from '../../components/ErrorPage';
import LoadingPage from '../../components/LoadingPage';
import Playlist from '../../components/Playlist';
import { FETCH_COURSE, FETCH_COURSES_BY_TOPIC, FETCH_COURSES_BY_TOPIC_FEED, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { Content, Course, Activity, SubTopic } from '../../types/Course';
import { User } from '../../types/User';
import ActivityPage from '../Activity';
import Video from '../Video';
import ReactGA from 'react-ga';

import './styles.css';

const TopicPage: React.FC<any> = ({ history, dispatch, user }) => {
  const [subtopics, setSubtopics] = useState<SubTopic[]>([]);
  const [courses, setCourses] = useState<Course[]>([]);

  const [loading, setLoading] = useState(true);

  const [error, setError] = useState('');
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchCoursesByTopicFeed] = useMutation(FETCH_COURSES_BY_TOPIC_FEED);
  
  const { id } = useParams<any>();

  const listRef = useRef(null);

  useEffect(() => {
    userVerify();
    ReactGA.pageview(window.location.pathname);
  }, []);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseCourse = await fetchCoursesByTopicFeed({ variables: { id: Number(id) } });
      const { coursesByTopicFeed } = responseCourse.data;

      dispatch(setUser(account));
      setSubtopics(coursesByTopicFeed.subtopics);
      setCourses(coursesByTopicFeed.courses);

      setLoading(false);   
    } catch(error: any) {
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;

  return (
    <div className={`basic-screen-inner topic`} ref={listRef}>
      { subtopics.length && <h2>Sub Tpicos</h2> }
      { subtopics.map((subtopic: SubTopic) => (
        <Block className="course-vertical-block topic-vertical-block" onClick={() => history.push(`/sub-topico/${subtopic.id}`)}>
          <BlockTitle title={subtopic.name} />
        </Block>
      )) }

      { courses.length && <h2>Cursos</h2> }

      { courses.map((course: Course) => (
          <Block className="course-vertical-block" onClick={() => history.push(`/curso/${course.id}`)}>
            <div className="course-item-list-image" style={{ backgroundImage: `url(${course.course_image})`, borderRadius: '12px' }}>
              <div className="course-list-course-image-university">
                <img src={course.institute.image} />
              </div>
              { course.percentageWatched && <div className="course-list-course-progress">{ course.percentageWatched.toFixed(0) }% Concludo!</div> }
            </div>
            <div className="info">
              <b>{course.name}</b>
              <span>{ course.institute.name }</span>
              <p>{ course.copy }</p>
              { course.percentageWatched ? <button>Continuar</button> : <button>Comear</button> }
            </div>
          </Block>
        )) }
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(TopicPage);