import { useHistory } from "react-router-dom";

import { Input } from "../../components/Form";

import { MdEmail, MdLock } from 'react-icons/md';
import { Link } from "react-router-dom";
import ReactGA from 'react-ga';

import './styles.css';

import { StartForm, StartFormScreen, StartFormTitle, StartFormButton, StartFormDescription } from "../../components/StarterFormScreen";
import { useEffect, useState } from "react";
import { useMutation } from "@apollo/client";
import { LOGIN } from "../../graphql/mutations";
import { setUser } from "../../store/actions";
import { connect } from "react-redux";

const Login: React.FC<any> = ({ dispatch }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [verified, setVerified] = useState(false);

  const [login] = useMutation(LOGIN);

  const history = useHistory();

  useEffect(() => {
    tokenVerify();
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  const tokenVerify = () => {
    if(localStorage.getItem('token')) history.push('/inicio');
    setVerified(true);
  }

  const closeError = () => setError('');

  const validateInputs = () => {
    if(!email) throw new Error('Digite o seu Email!');
    if(!password) throw new Error('Digite a sua Senha!');
  }

  const saveToken = (token: string) => localStorage.setItem('token', token);
  const goToDashboardScreen = () => history.push('/inicio');

  const onSubmit = async (e: React.FormEvent<EventTarget>) => {
    e.preventDefault();

    if(loading) return;

    try {
      validateInputs();
      setLoading(true);

      const response = await login({ variables: { email, password } });
      const { token, user } = response.data.login;
      
      saveToken(token);
      dispatch(setUser(user));
      
      if(!user.selected_interest) {
        history.push('/selecao-interesse');
        return;
      }

      goToDashboardScreen();
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        setError('Erro ao Conectar ao Servidor');
      else
        setError(error.message);
    }
  }

  if(!verified) return <div className="login"></div>;

  return (
    <StartFormScreen>
      <div className="login-form-container">
      <StartForm onSubmit={onSubmit}>
        <StartFormTitle title="Entrar" />
        { !error ? <StartFormDescription text="Para acessar o Instrue, insira o seu email e senha." /> : null }
        { error ? <StartFormDescription text={error} className="login-error" /> : null  }
        <Input Icon={MdEmail} placeholder="Email" type="email" value={email} onChangeText={setEmail} required={true} />
        <Input Icon={MdLock} placeholder="Senha" type="password" value={password} onChangeText={setPassword} required={true} />
        { loading ? <StartFormButton name="Entrando.." /> : <StartFormButton name="Entrar" /> }
      </StartForm>
        <div className="login-redirects">
          <Link to="/cadastrar">No tem uma conta? <span>Cadastre-se</span></Link>
        </div>
      </div>
    </StartFormScreen>
  );
}

export default connect()(Login);