import { useMutation } from '@apollo/client';
import React, { useState } from 'react';
import { MdCheckCircle, MdDelete } from 'react-icons/md';
import { useHistory } from 'react-router-dom';
import { Block as BlockComponent, BlockTitle } from '../../components/Block';
import { DELETE_ACTIVITIES_BLOCK, DELETE_BLOCK, DELETE_LESSON } from '../../graphql/mutations';
import { ActivitiesBlock, Activity, Block, Content, Course, Lesson } from '../../types/Course';
import { Modal } from '../Modal';

import './styles.css';

interface CourseVideoProps {
  id: string,
  videoNumber: number,
  title: string,
  isAuthor: boolean,
  finished?: boolean,
  onClick?: () => void,
  onClickDelete?: () => void
}

interface CourseActivitiesBlockProps {
  id: string,
  activitiesBlockNumber: number,
  title: string,
  exercisesNumber: number,
  isAuthor: boolean,
  finished?: boolean,
  onClick?: () => void,
  onClickDelete?: () => void
}

interface CourseCertificateProps {
  onClick?: () => void
}

interface PlaylistProps {
  className?: string,
  blocks?: Block[],
  isAuthor?: boolean,
  onContentClick: (content: Content) => void,
  onCertificateClick: () => void
  onCreateBlockClick?: () => void,
  onCreateLessonClick?: (block: Block) => void,
  onCreateActivitiesBlockClick?: (block: Block) => void,
  onDeleteContent?: (course: Course) => void
}

const CourseCertificate: React.FC<CourseCertificateProps> = ({ onClick }) => {
  return (
    <div className={`course-video`} onClick={onClick}>
      <div className="course-video-title">
        <b>Certificado</b>
      </div>
      <span>Obter Certificado</span>
    </div>
  );
}

const CourseVideo: React.FC<CourseVideoProps> = ({ id, onClick, videoNumber, title, finished, isAuthor, onClickDelete }) => {
  return (
    <div className={`course-video ${finished ? 'course-video-finished' : ''}`} onClick={onClick}>
      <div className="course-video-title">
        <b>Aula { videoNumber }</b>
      </div>
      { !isAuthor ? finished ? <MdCheckCircle size="22" className="course-video-finished-icon" /> : null : null }
      { isAuthor ? <MdDelete size="22" className="course-video-finished-icon" onClick={onClickDelete} /> : null }
      <span>{ title }</span>
    </div>
  );
}

const CourseActivitiesBlock: React.FC<CourseActivitiesBlockProps> = ({ id, onClick, activitiesBlockNumber, title, exercisesNumber, finished, isAuthor, onClickDelete }) => {
  return (
    <div className={`course-video ${finished ? 'course-video-finished' : ''}`} onClick={onClick}>
      <div className="course-video-title">
        <b>Exerccio { activitiesBlockNumber }</b>
      </div>
      { finished ? <MdCheckCircle size="22" className="course-video-finished-icon" /> : null }
      { isAuthor ? <MdDelete size="22" className="course-video-finished-icon" onClick={onClickDelete}  /> : null }
      <span>{ title }</span>
      <span>{exercisesNumber} Atividades</span>
    </div>
  );
}

const Playlist: React.FC<PlaylistProps> = ({ className, blocks, onContentClick, onCertificateClick, onDeleteContent, onCreateBlockClick, onCreateLessonClick, onCreateActivitiesBlockClick, isAuthor }) => {
  const [deletingLesson, setDeletingLesson] = useState(false);
  const [deletingActivity, setDeletingActivity] = useState(false);
  const [deletingBlock, setDeletingBlock] = useState(false);

  const [deleteLesson] = useMutation(DELETE_LESSON);
  const [deleteActivity] = useMutation(DELETE_ACTIVITIES_BLOCK);
  const [deleteBlock] = useMutation(DELETE_BLOCK);

  blocks = blocks || [];
  
  let videoNumber = 0;
  let activitiesBlockNumber = 0;

  const onClickDeleteLesson = async (e: any, lesson: Lesson) => {
    e.stopPropagation();

    if(deletingLesson) return;

    try {
      setDeletingLesson(true);

      const response = await deleteLesson({ variables: { id: Number(lesson.id) } });
      /* @ts-ignore */
      onDeleteContent(response.data.deleteLesson);

      setDeletingLesson(false);
    } catch(error: any) {
      setDeletingLesson(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  const onClickDeleteActivity = async (e: any, activity: ActivitiesBlock) => {
    e.stopPropagation();

    if(deletingActivity) return;

    try {
      setDeletingActivity(true);

      const response = await deleteActivity({ variables: { id: Number(activity.id) } });
      /* @ts-ignore */
      onDeleteContent(response.data.deleteActivitiesBlock);

      setDeletingActivity(false);
    } catch(error: any) {
      setDeletingActivity(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  const onClickDeleteBlock = async (block: Block) => {
    if(deletingBlock) return;

    try {
      setDeletingBlock(true);

      const response = await deleteBlock({ variables: { id: Number(block.id) } });
      /* @ts-ignore */
      onDeleteContent(response.data.deleteBlock);

      setDeletingBlock(false);
    } catch(error: any) {
      console.log(error.message);
      setDeletingBlock(false);
      if(error.message.indexOf('Cannot delete or update a parent row: a foreign key constraint fails') != -1)
        alert('Delete as Lies e Atividades do Bloco Primeiro');
      else if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  return (
    <div className={`course-playlist ${className}`}>
      { blocks.map((block: Block) => {
        videoNumber = 0;
        activitiesBlockNumber = 0;
        return <BlockComponent key={`block${block.id}`} width="100%" className="course-topics-list-block">
          { isAuthor ? <MdDelete size="22" className="course-video-finished-icon" onClick={() => onClickDeleteBlock(block)} /> : null }
          <h2>{ block.name }</h2>
          <div className="course-topics-list-number-container">
            <span className="course-topics-list-number">{ block.contents.length } Contedos</span>
          </div>
          { block.contents.map((content: Content) => {
            if(content.lesson) {
              videoNumber+=1;
              /* @ts-ignore */
              return <CourseVideo key={`content${content.id}`} isAuthor={isAuthor} id={content.lesson.id.toString()} videoNumber={videoNumber} title={content.name} finished={content.watched} onClick={() => { window.scrollTo(0, 0); onContentClick(content)}} onClickDelete={(e) => onClickDeleteLesson(e, content.lesson)} />;
            } else if(content.activitiesblock) {
              let activitiesDidNumber = 0;
              content.activitiesblock.activities.forEach((activity: Activity) => {
                if(activity.user_answer_id) activitiesDidNumber += 1;
              });
              activitiesBlockNumber+=1;
              /* @ts-ignore */
              return <CourseActivitiesBlock key={`content${content.id}`} isAuthor={isAuthor} id={content.activitiesblock.id.toString()} activitiesBlockNumber={activitiesBlockNumber} title={content.name} finished={activitiesDidNumber ? activitiesDidNumber == content.activitiesblock.exercises_number : false} onClick={() => { window.scrollTo(0, 0); onContentClick(content)}} exercisesNumber={content.activitiesblock.exercises_number} onClickDelete={(e) => onClickDeleteActivity(e, content.activitiesblock)} />;
            }
            return null;
          }) }
          { /* @ts-ignore */ }
          { isAuthor ? <span onClick={() => onCreateLessonClick(block)} className="add-author">Adicionar Vdeoaula</span> : null }
          { isAuthor ? <br></br>: null }
          { /* @ts-ignore */ }
          { isAuthor ? <span onClick={() => onCreateActivitiesBlockClick(block)} className="add-author">Adicionar Exerccios</span> : null }
        </BlockComponent>   
      }) }
      { isAuthor ? <BlockComponent width="100%" className="course-topics-list-block">
        <span className="add-author" onClick={onCreateBlockClick}>Adicionar Bloco</span> 
      </BlockComponent> : null }
      { !isAuthor && (
        <BlockComponent width="100%" className="course-topics-list-block">
          <CourseCertificate onClick={() => {
            window.scrollTo(0, 0);
            onCertificateClick();
          }} />
        </BlockComponent>
      ) }
      { deletingActivity || deletingLesson || deletingBlock ? <Modal><BlockTitle title="Deletando..." /></Modal> : null }
    </div>
  );
}

export default Playlist;