import React from "react";
import { MdClose } from "react-icons/md";
import './styles.css';

export interface ModalProps {
  children: React.ReactNode,
  className?: string,
  onExitClick?: () => void
};

const Modal: React.FC<ModalProps> = ({ children, className, onExitClick }) => {
  return (
    <div className="modal-background">
      { onExitClick ? <MdClose size={30} className="modal-close" onClick={onExitClick} /> : null }
      <div className={`modal ${className}`}>
          { children }
      </div>
    </div>
  );
}

export { Modal };