import { SET_USER } from "../actionTypes";

const initialState = {
  user: null,
  campusId: null
}

const f = (state = initialState, action: any) => {
  switch (action.type) {
    case SET_USER: {
      const { user } = action.payload;
      return {
          ...state,
          user
        };
      }     
    default:
      return state;
  }
}

export default f;