function convertTextToHtml(text: string) {
  text = text.replaceAll('[Imagem: ', '<img src="');
  text = text.replaceAll('.jpg]', '.jpg"/>');
  text = text.replaceAll('.png]', '.png"/>');

  let newtext = '';

  text.split('\n').forEach((p: string) => p != '' ? p.indexOf('img') == -1 ? newtext += `<p>${p}</p>` : newtext += p : null);

  return newtext;
}

export default convertTextToHtml;