import { useMutation } from '@apollo/client';
import { useEffect, useState } from 'react';
import { BsClockFill } from 'react-icons/bs';
import { MdArrowBack, MdArrowForward } from 'react-icons/md';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import ErrorPage from '../../components/ErrorPage';
import LoadingPage from '../../components/LoadingPage';
import { FETCH_FEED_PRINCIPAL, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { BlockFeed } from '../../types/Course';
import HorizontalCourseList from './HorizontalCourseList';
import ReactGA from 'react-ga';

import './styles.css';

const Start: React.FC<any> = ({ history, dispatch, user }) => {
  const [loading, setLoading] = useState(true);

  const [error, setError] = useState('');
  const [blocks, setBlocks] = useState([]);
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchFeedNew] = useMutation(FETCH_FEED_PRINCIPAL);

  useEffect(() => {
    userVerify();
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      dispatch(setUser(account));

      if(!account.selected_interest) {
        history.push('/selecao-interesse');
        return;
      }

      const responseFeed = await fetchFeedNew();
      const { feedPrincipalMutation } = responseFeed.data;

      setBlocks(feedPrincipalMutation.blocks);

      setLoading(false);  
    } catch(error: any) {
      console.log(error);
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;

  return (
    <div className={`basic-screen-inner start`}>
      <div className='start-left'>
        <img src="./instrue-info.png" />
        <div className='topics-list'>
          { blocks.map((block: BlockFeed) => block.topic_id ? <Link to={`/topico/${block.topic_id}`}>{ block.name }</Link> : null) }
        </div>
        { blocks.map((block: BlockFeed) => <HorizontalCourseList title={block.name} courses={block.courses} topicId={block.topic_id} />) }
      </div>
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(Start);