import { useMutation } from '@apollo/client';
import React, { useState } from 'react';
import { MdInfo } from 'react-icons/md';
import { BlockTitle } from '../../components/Block';
import { Button, Form, Input } from '../../components/Form';
import { Modal } from '../../components/Modal';
import { UPDATE_USER } from '../../graphql/mutations';

import './styles.css';

interface Props {
  onUpdate: () => void
}

const CPFModal: React.FC<Props> = ({ onUpdate }: Props) => {
  const [cpf, setCpf] = useState('');
  const [loading, setLoading] = useState(false);
  const [updateUser] = useMutation(UPDATE_USER);

  const onSubmit = async (e: any) => {
    e.preventDefault();

    if(loading) return;

    try {
      setLoading(true);

      await updateUser({ variables: { 
        cpf
      } });

      onUpdate()
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }
  return (
    <Modal className='cpf-modal'>
      <BlockTitle title="CPF" />
      <p>Para adicionarmos o seu CPF no seu certificado, insira ele abaixo.</p>
      <Form onSubmit={onSubmit}>
        <Input Icon={MdInfo} placeholder="Nmero do CPF (Apenas os nmeros)" type="text" onChangeText={setCpf} value={cpf} required={true} />
        { loading ? <Button name="Adicionando.." type="button" onClick={() => {}} /> : <Button type="submit" name="Adicionar" /> }
      </Form>
    </Modal>
  );
}

export default CPFModal;