import { useMutation } from '@apollo/client';
import { useEffect, useRef, useState } from 'react';
import { MdInfo } from 'react-icons/md';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { Block, BlockTitle } from '../../components/Block';
import ErrorPage from '../../components/ErrorPage';
import { Button, Form, Input, Select, Option, Textarea } from '../../components/Form';
import LoadingPage from '../../components/LoadingPage';
import { Modal } from '../../components/Modal';
import { CREATE_COURSE } from '../../graphql/mutations';
import { COURSES_BY_USER, FETCH_INSTITUTES, FETCH_TOPICS, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { Course, SubTopic, Topic } from '../../types/Course';
import { Institute } from '../../types/Institute';
import uploadImgurAPI from '../../utils/uploadImgurAPI';
import ReactGA from 'react-ga';

import './styles.css';

const UserCourses: React.FC<any> = ({ history, dispatch, user }) => {
  const [topics, setTopics] = useState<Topic[]>([]);
  const [institutes, setInstitutes] = useState<Institute[]>([]);
  const [subtopics, setSubTopics] = useState<Option[]>([]);
  const [loading, setLoading] = useState(true);
  const [loadingNew, setLoadingNew] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const [error, setError] = useState('');
  const [courses, setCourses] = useState<Course[]>([]);

  const [image, setImage] = useState<any>(null);
  const [name, setName] = useState('');
  const [copy, setCopy] = useState('');
  const [description, setDescription] = useState('');
  const [workload, setWorkload] = useState('');
  const [requirements, setRequirements] = useState('');
  const [institute_id, setInstitute] = useState('');
  const [topic_id, setTopic] = useState('');
  const [subtopic_id, setSubTopic] = useState('');
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchCoursesByUser] = useMutation(COURSES_BY_USER);
  const [fetchTopics] = useMutation(FETCH_TOPICS);
  const [fetchInstitutes] = useMutation(FETCH_INSTITUTES);

  const [createCourse] = useMutation(CREATE_COURSE);

  const fileRef = useRef(null);

  const { id } = useParams<any>();

  useEffect(() => {
    userVerify();
    ReactGA.pageview(window.location.pathname);
  }, []);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseFeed = await fetchCoursesByUser({ variables: { user_id: Number(id) } });
      const { coursesByUser } = responseFeed.data;

      const responseCourse = await fetchTopics();
      const { topicsMutation } = responseCourse.data;

      const responseInstitutes = await fetchInstitutes();
      const { institutesMutation } = responseInstitutes.data;

      setTopics(topicsMutation);
      setInstitutes(institutesMutation);

      dispatch(setUser(account));
      setCourses(coursesByUser);

      setLoading(false);  
    } catch(error: any) {
      console.log(error);
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  const onChangeTopic = (id: string) => {
    setTopic(id);
    const sub: Option[] = [];

    topics.forEach((topic: Topic) => {
      if(topic.id.toString() == id) {
        /* @ts-ignore */
        topic.subtopics.forEach((subto: SubTopic) => sub.push({ name: subto.name, value: subto.id }));
      }
    });

    setSubTopics(sub);
  }

  const onSelectImageClick = () => {
    /* @ts-ignore */
    fileRef.current.click();
  }

  const onChangeFile = (e: any) => {
    /* @ts-ignore */
    setImage(e.target.files[0]);
  }

  const onSubmit = async (e: any) => {
    e.preventDefault();

    if(!image) return alert('Adicione uma Imagem do Curso');
    if(loading) return;

    try {
      setLoadingNew(true);

      const imageUploaded: any = await uploadImgurAPI(image);
      const course_image = imageUploaded.data.link;

      const response = await createCourse({ variables: {
        name,
        course_image,
        copy,
        description,
        requirements,
        workload: Number(workload),
        institute_id: Number(institute_id),
        topic_id: Number(topic_id),
        subtopic_id: Number(subtopic_id)
      } });

      const { id } = response.data.createCourse;

      history.push(`/curso/${id}`);
    } catch(error: any) {
      setLoadingNew(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        setError('Erro ao Conectar ao Servidor');
      else
        setError(error.message);
    }
  } 

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;

  const topicsOptions: Option[] = [];
  const institutesOptions: Option[] = [];

  topics.forEach((topic: Topic) => { topicsOptions.push({name: topic.name, value: topic.id.toString() }) })
  institutes.forEach((institute: Institute) => { institutesOptions.push({ name: institute.name, value: institute.id.toString() }) })

  return (
    <div className={`basic-screen-inner topic`}>
      { user.id == id ? <div style={{ marginTop: '24px' }}><BlockTitle title="Meus Cursos" /></div> : null }
      { user.id == id ? <Block width="90%" className="course-vertical-block course-add-course" onClick={() => setIsModalOpen(true)}><BlockTitle title="Adicionar Curso" /></Block> : null }
      { courses.map((course: Course) => (
          <Block width="90%" className="course-vertical-block" onClick={() => history.push(`/curso/${course.id}`)}>
            <div className="course-item-list-image" style={{ backgroundImage: `url(${course.course_image})` }}></div>
            <div className="info">
              <b>{course.name}</b>
              <span>{ course.institute.name }</span>
              <span>{ course.copy }</span>
            </div>
          </Block>
        )) }
      { isModalOpen ? <Modal className="add-modal" onExitClick={() => setIsModalOpen(false)}>
        <BlockTitle title="Adicionar" />
        <Form onSubmit={onSubmit}>
          { !image ? <Button name="Selecionar Imagem" type="button" onClick={onSelectImageClick} /> :  <Button name="Imagem Selecionada" type="button" onClick={() => {}} /> }
          <input type="file" style={{ display: 'none' }} accept="image/png, image/gif, image/jpeg" ref={fileRef} onChange={onChangeFile} />
          <Input Icon={MdInfo} placeholder="Nome do Curso" type="text" onChangeText={setName} value={name} required={true} />
          <Textarea placeholder="Descrio Curta e Chamativa" onChangeText={setCopy} value={copy} required={true} />
          <Textarea placeholder="Descrio do Curso" onChangeText={setDescription} value={description} required={true} />
          <Textarea placeholder="Pr-Requisitos (Opcional)" onChangeText={setRequirements} value={requirements} />
          <Input Icon={MdInfo} placeholder="Durao do Curso em Horas" type="number" onChangeText={setWorkload} value={workload} required={true} />
          <Select Icon={MdInfo} placeholder="Instituto" options={institutesOptions} onChangeText={setInstitute} value={institute_id} required={true} />
          <Select Icon={MdInfo} placeholder="Tpico" options={topicsOptions} onChangeText={onChangeTopic} value={topic_id} required={true} />
          { topic_id ? <Select Icon={MdInfo} placeholder="Sub Tpico" options={subtopics} onChangeText={setSubTopic} value={subtopic_id} required={true} /> : null }
          { loadingNew ? <Button name="Criando.." type="button" onClick={() => {}} /> : <Button name="Criar" type="submit" /> }
        </Form>
      </Modal> : null }
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(UserCourses);