import { ApolloClient, ApolloProvider, createHttpLink, InMemoryCache } from '@apollo/client';
import { setContext } from '@apollo/client/link/context';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import {
  HashRouter as Router,
  Switch,
  Route,
  useLocation,
} from "react-router-dom";
import { BasicScreen } from './components/BasicScreen';
import { ContentMenu, ContentMenuItem } from './components/ContentMenu';
import ReactGA from 'react-ga';
import './index.css';

import reportWebVitals from './reportWebVitals';
import Course from './screens/Course';
import InterestSelection from './screens/InterestSelection';
import Login from './screens/Login';
import Search from './screens/Search';
import Start from './screens/Start';
import StartAll from './screens/StartAll';
import StartNew from './screens/StartNew';
import Topic from './screens/Topic';
import store from './store';
import UserCourses from './screens/UserCourses';
import Register from './screens/Register';
import Subtopic from './screens/Subtopic';
import CertificateList from './screens/CertificateList';
import LeftMenu from './components/LeftMenu';

// https://graphql.instrue.com.br/graphql
// http://localhost:5000/graphql
const httpLink = createHttpLink({
  uri: 'https://graphql.instrue.com.br/graphql',
});

/* @ts-ignore */
const authLink = setContext(async (_, { headers }) => {
  const token = localStorage.getItem('token');
  return {
    headers: {
      ...headers,
      authorization: token ? `Bearer ${token}` : "",
    }
  }
});

const client = new ApolloClient({
  link: authLink.concat(httpLink),
  cache: new InMemoryCache()
});

ReactGA.initialize('UA-232898176-1');

ReactDOM.render(
<React.StrictMode>
  <Provider store={store}> 
  <ApolloProvider client={client}>
  <Router>
    <Switch>
      <Route path="/" exact component={Login} />
      <Route path="/cadastrar" exact component={Register} />
      <div className="basic-container">
        <LeftMenu />
        <BasicScreen>
          <div className="basic-second-container">
            <Switch>
              <Route path="/inicio/" exact component={Start} />
              <Route path="/inicio/novos/" exact component={StartNew} />
              <Route path="/inicio/todos/" exact component={StartAll} />
              <Route path="/curso/:id/" exact component={Course} />
              <Route path="/topico/:id/" exact component={Topic} />
              <Route path="/sub-topico/:id/" exact component={Subtopic} />
              <Route path="/selecao-interesse/" exact component={InterestSelection} />
              <Route path="/pesquisar/:query/" exact component={Search} />
              <Route path="/cursos-professor/:id" exact component={UserCourses} />
              <Route path="/certificados/" exact component={CertificateList} />
            </Switch>
          </div>
        </BasicScreen>
      </div>
    </Switch>
  </Router>
  </ApolloProvider>
  </Provider>
</React.StrictMode>
, document.getElementById('root')
);

reportWebVitals();

