import { gql } from '@apollo/client';

const FETCH_USER = gql`
mutation {
  userMutation {
    id
    name
    email
    photo_url
    selected_interest
    teacher_verified
    cpf
  }
}
`;

const FETCH_COURSE = gql`
mutation fetchCourse($id: Float!) {
    courseMutation(id: $id) {
      id
      name
      course_image
      description
      requirements
      certificate {
        id
        code
        workload
        course_name
        user_name
        teacher_name
        date_created
      }
      published
      started
      blocks {
        id
        name
        position
        contents {
          id
          name
          position
          watched
          lesson {
            id
            description
            youtube_link
            attachments {
              id
              name
              link
            }
          }
          activitiesblock {
            id
            exercises_number
            activities {
              id
              text
              made_by_me
              free_for_use
              correct_alternative_id
              correction_link
              correction_text
              correction_youtube_link
              user_answer_id
              alternatives {
                id
                value
              }
            }
          }
        }
      }
      copy
      institute {
        id
        name
        image
        white_image
        description
      }
      teachers {
        id
        name
        photo_url
        description
      }
    }
  }
`;

