import './styles.css';

import { MdVideocam, MdHome, MdBook, MdExitToApp, MdDashboard, MdExplore, MdCheck, MdTv } from 'react-icons/md';
import { useHistory, useLocation } from 'react-router-dom';
import { connect } from 'react-redux';
import { User } from '../../types/User';

interface LeftMenuProps {
  Icon: any,
  title: string,
  to?: string,
  selected?: boolean,
  onClick?: () => void
};

const LeftMenuButton: React.FC<LeftMenuProps> = ({ Icon, title, selected, to, onClick }) => {
  const history = useHistory();

  const goTo = () => {
    if(to)
      history.push(to);
  }

  if(onClick) {
    return (
      <div onClick={onClick} className="left-menu-button"> 
        <Icon size={20} />
        <span>{ title }</span>
    </div>
    )
  }

  if(selected) {
    return (
      <div onClick={goTo} className="left-menu-button left-menu-button-selected"> 
        <Icon size={20} />
        <span>{ title }</span>
      </div>
    );
  }
  return (
    <div onClick={goTo} className="left-menu-button"> 
      <Icon size={20} />
      <span>{ title }</span>
    </div>
  );
}

const LeftMenu: React.FC<any> = ({ user }) => {
  const history = useHistory();

  const location = useLocation();
  const pathName = location.pathname;

  const exit = () => {
    localStorage.setItem('token', '');
    history.push('/');
  }
  
  return (
    <div className="left-menu">
      <h1>Instrue</h1>
      <LeftMenuButton Icon={MdDashboard} title="Menu Principal" selected={pathName === '/inicio/' || pathName === '/inicio'} to="/inicio" />
      <LeftMenuButton Icon={MdExplore} title="Explorar Cursos" selected={pathName === '/inicio/todos/'} to="/inicio/todos/" />
      <LeftMenuButton Icon={MdCheck} title="Meus Certificados" selected={pathName === '/certificados/'} to="/certificados/" />
      { user ? user.teacher_verified && <LeftMenuButton Icon={MdTv} title="Meus Cursos" selected={pathName === `/cursos-professor/${user.id}`} to={`/cursos-professor/${user.id}`} /> : null}
      <LeftMenuButton Icon={MdExitToApp} title="Sair" onClick={exit} />
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(LeftMenu);