import React from "react";
import { useHistory } from "react-router-dom";
import { Institute } from "../../types/Institute";
import './styles.css';

export interface CourseItemListProps {
  id: string,
  image: string,
  title: string,
  copy: string,
  institute: Institute,
  percentageWatched?: number
};

export interface CourseListProps {
  children: React.ReactNode,
  className?: string
}

const CourseItemList: React.FC<CourseItemListProps> = ({ id, image, title, copy, institute, percentageWatched }) => {
  const history = useHistory();
  percentageWatched = percentageWatched || 0;

  const goToCourse = () => {
    history.push(`/curso/${id}`);
  }

  return (
    <div className="course-item-list" onClick={goToCourse}>
      <div className="course-item-list-image" style={{ backgroundImage: `url(${image})` }}>
        <div className="course-item-list-university">
          <img src={institute.image} />
        </div>
        <div className="course-item-list-image-percentage" style={{ width: `${percentageWatched}%` }}></div>
      </div>
      <div className="course-item-list-text">
        <b>{ title }</b>
        <p>
          { copy }
        </p>
      </div>
    </div>
  );
}


const CourseList: React.FC<CourseListProps> = ({ children, className }) => {
  return (
    <div className={`course-list ${className}`}>
      { children }
    </div>
  );
}

export { CourseItemList, CourseList };