import { useMutation } from '@apollo/client';
import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import ErrorPage from '../../components/ErrorPage';
import { Button } from '../../components/Form';
import LoadingPage from '../../components/LoadingPage';
import Playlist from '../../components/Playlist';
import { ADD_INTEREST_TOPICS } from '../../graphql/mutations';
import { FETCH_COURSE, FETCH_TOPICS, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { Content, Course, Activity, Topic } from '../../types/Course';
import { User } from '../../types/User';
import ActivityPage from '../Activity';
import Video from '../Video';
import ReactGA from 'react-ga';

import './styles.css';

export interface TopicItemProps {
  topic: Topic,
  selected: boolean,
  onClick: (topic: Topic) => void
}

const TopicItem: React.FC<TopicItemProps> = ({ topic, selected, onClick }) => {
  return (
    <div className={`topic-item ${selected ? 'topic-item-selected' : ''}`} onClick={() => onClick(topic)}>
      { topic.name }
    </div>
  );
}

const InterestSelection: React.FC<any> = ({ history, dispatch, user }) => { 
  const [topics, setTopics] = useState<Topic[]>([]);
  const [topicsSelected, setTopicsSelected] = useState<number[]>([]);

  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState('');

  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchTopics] = useMutation(FETCH_TOPICS);
  const [addInterestTopics] = useMutation(ADD_INTEREST_TOPICS);

  useEffect(() => {
    userVerify();
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseCourse = await fetchTopics();
      const { topicsMutation } = responseCourse.data;

      if(account.selected_interest) {
        goToDashboardScreen();
        return;
      }

      dispatch(setUser(account));
      setTopics(topicsMutation);

      setLoading(false);      
    } catch(error: any) {
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  const isInSelection = (topic: Topic) => {
    return topicsSelected.indexOf(Number(topic.id)) !== -1;
  }

  const onTopicClick = (topic: Topic) => {
    if(isInSelection(topic)) {
      let newTopicsSelected = topicsSelected.filter((topicId: number) => topicId != Number(topic.id));
      setTopicsSelected(newTopicsSelected);
      return;
    }

    setTopicsSelected([...topicsSelected, Number(topic.id)]);
  }

  const goToDashboardScreen = () => history.push('/inicio');

  const onSubmit = async () => {
    if(submitting) return;

    try {
      setSubmitting(true);

      await addInterestTopics({ variables: { topicsSelected } });
      
      dispatch(setUser({...user, selected_interest: 1}));

      goToDashboardScreen();
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        setError('Erro ao Conectar ao Servidor');
      else
        setError(error.message);
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;

  return (
    <BasicScreenInner className="interest-selection">
      <Block width="80%" className="interest-selection-block">
        <BlockTitle title="Selecione os Tpicos que Voc tem Interesse" />
        <span className="interest-selection-subtitle">Selecione pelo menos um tpico para utilizar a plataforma</span>
        <div className="interest-selection-topics-list">
          { topics.map((topic: Topic) => <TopicItem topic={topic} selected={isInSelection(topic)} onClick={onTopicClick} key={`topic${topic.id}`} />) }
        </div>
        { submitting ? <Button name="Selecionando.." /> : null }
        { topicsSelected.length && !submitting ? <Button name="Selecionar" onClick={onSubmit} /> : null }
      </Block>
    </BasicScreenInner>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(InterestSelection);