import { Button, Form } from "../../components/Form"

import './styles.css';

export interface StartFormTitleProps {
  title: string
}

export interface StartFormDescriptionProps {
  text: string,
  className?: string
}

export interface StartFormProps {
  children: React.ReactNode,
  onSubmit?: (e: React.FormEvent<EventTarget>) => void
}

export interface StartFormButtonProps {
  name: string,
  type?: any,
  onClick?: () => void
}

export interface StartFormScreenProps {
  children: React.ReactNode
}

const StartFormTitle: React.FC<StartFormTitleProps> = ({ title }) => {
  return <h1 className="start-form-title">{ title }</h1>;
}

const StartFormDescription: React.FC<StartFormDescriptionProps> = ({ text, className }) => {
  return <p className={`start-form-description ${className}`}>{ text }</p>;
}

const StartForm: React.FC<StartFormProps> = ({ children, onSubmit }) => {
  return (
    <Form className="start-form" onSubmit={onSubmit}>
      { children }
    </Form>
  );
}

const StartFormButton: React.FC<StartFormButtonProps> = ({ name, type, onClick }) => {
  type = type || 'submit';
  return (
    <div className="start-form-button-container">
      <Button name={name} type={type} onClick={onClick} />
    </div>
  );
}

const StartFormScreen: React.FC<StartFormScreenProps> = ({ children }) => {
  return (
    <div className="start-form-container">

      <div className="start-container-left">
        { children }
        <div className="start-details start-details-top">
        <div className="start-details-row">
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
        </div>
        <div className="start-details-row">
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
        </div>
        <div className="start-details-row">
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
        </div>
      </div>

      <div className="start-details start-details-bottom">
        <div className="start-details-row">
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
        </div>
        <div className="start-details-row">
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
        </div>
        <div className="start-details-row">
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
          <div>+</div>
        </div>
      </div>
      </div>
      <div className="start-container-right">
        <span>O Instrue  a <br/><b>Universidade</b><br/>Totalmente <b>Livre</b><br />e <b>Gratuita</b></span>
      </div>
    </div>
  );
}

export { StartFormTitle, StartFormDescription, StartForm, StartFormButton, StartFormScreen };