import { Checkbox, CheckboxText, Input, Select } from "../../components/Form";

import { MdPerson, MdEmail, MdLock, MdMap, MdLocationOn } from 'react-icons/md';
import ReactGA from 'react-ga';

import './styles.css';

import { StartForm, StartFormScreen, StartFormTitle, StartFormButton, StartFormDescription } from "../../components/StarterFormScreen";
import { useEffect, useState } from "react";
import { connect } from "react-redux";
import { setUser } from "../../store/actions";
import { useMutation } from "@apollo/client";
import { LOGIN, REGISTER } from "../../graphql/mutations";
import states from "../../values/states";
import cities from "../../values/cities";
import userTypes from "../../values/userTypes";

const Register: React.FC<any> = ({ dispatch, history }) => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [repeatPassword, setRepeatPassword] = useState('');

  const [state, setState] = useState('');
  const [city, setCity] = useState('');
  const [userType, setUserType] = useState('');
  const [termsAccepted, setTermsAccepted] = useState(false);
  const [receiveUpdates, setReceiveUpdates] = useState(false);

  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [step, setStep] = useState(0);

  const [register] = useMutation(REGISTER);
  const [login] = useMutation(LOGIN);

  useEffect(() => {
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);

  const validateInputsStepOne = (e: React.FormEvent<EventTarget>) => {
    e.preventDefault();
    setError('');
    if(!name) return setError('Digite o seu Nome Completo!');
    if(!email) return setError('Digite o seu Email!');
    if(!password) return setError('Digite a sua Senha!');
    if(!repeatPassword) return setError('Repita a Senha!');
    if(password != repeatPassword) return setError('As senhas no so iguais!');
    setStep(1);
  }

  const validateInputsStepThree = () => {
    if(!state) throw new Error('Selecione o seu Estado!');
    if(!city) throw new Error('Selecione sua Cidade!');
    if(!termsAccepted) throw new Error(' necessrio aceitar os termos de uso para usar a plataforma.');
  }

  const saveToken = (token: string) => localStorage.setItem('token', token);

  const onSubmit = async (e: React.FormEvent<EventTarget>) => {
    e.preventDefault();

    if(loading) return;

    try {
      validateInputsStepThree();
      setLoading(true);

      await register({ variables: {
        name,
        email,
        password,
        city_id: Number(city),
        receive_updates: receiveUpdates,
        user_type: userType
      } });

      const response = await login({ variables: { email, password } });
      const { token, user } = response.data.login;

      saveToken(token);
      dispatch(setUser(user));

      history.push('/selecao-interesse');
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        setError('Erro ao Conectar ao Servidor');
      else
        setError(error.message);
    }
  }

  if(!step) {
    return (
      <StartFormScreen>
        <StartForm onSubmit={validateInputsStepOne}>
          <StartFormTitle title="Cadastro" />
          { !error ? <StartFormDescription text="Para realizar o cadastro, voc deve fornecer algumas informaes bsicas." /> : null }
          { error ? <StartFormDescription text={error} className="login-error" /> : null }
          <Input Icon={MdPerson} placeholder="Nome Completo" type="text" value={name} onChangeText={setName} />
          <Input Icon={MdEmail} placeholder="Email" type="email" value={email} onChangeText={setEmail} />
          <Input Icon={MdLock} placeholder="Senha" type="password" value={password} onChangeText={setPassword} />
          <Input Icon={MdLock} placeholder="Repita a Senha" type="password" value={repeatPassword} onChangeText={setRepeatPassword} />
          <StartFormButton name="Continuar" />
        </StartForm>
      </StartFormScreen>
    );
  }

  return (
  <StartFormScreen>
    <StartForm onSubmit={onSubmit}>
      <StartFormTitle title="Cadastro" />
      { !error ? <StartFormDescription text="Para realizar o cadastro, voc deve fornecer algumas informaes bsicas." /> : null }
      { error ? <StartFormDescription text={error} className="login-error" /> : null }
      <Select Icon={MdMap} placeholder="Estado" options={states} onChangeText={setState} value={state} />
      { /* @ts-ignore */ }
      { state ? <Select Icon={MdLocationOn} placeholder="Cidade" options={cities[state]} onChangeText={setCity} value={city} /> : null }
      <Select Icon={MdPerson} placeholder="Voc " options={userTypes} onChangeText={setUserType} value={userType} />
      <div style={{ paddingTop: 6, paddingBottom: 12 }}>
        <Checkbox onChangeValue={setTermsAccepted} checked={termsAccepted}><CheckboxText>Eu Concordo Com os <a href="https://instrue.com.br/termos/" className="terms-highlight">Termos de Uso</a></CheckboxText></Checkbox>
        <Checkbox name="Desejo Receber Emails com Atualizaes" onChangeValue={setReceiveUpdates} checked={receiveUpdates} />
      </div>
      { loading ? <StartFormButton name="Cadastrando.." /> : <StartFormButton name="Cadastrar" /> }
    </StartForm>
  </StartFormScreen>
  );
}

export default connect()(Register);