import React from "react";
import './styles.css';

export interface BlockProps {
  children: React.ReactNode,
  className?: string,
  width?: string,
  onClick?: () => void
};

export interface BlockTitleProps {
  title: string
}

const Block: React.FC<BlockProps> = ({ children, width, className, onClick }) => {
  return (
    <div className={`block ${className}`} onClick={onClick}>
      { children }
    </div>
  );
}

const BlockTitle: React.FC<BlockTitleProps> = ({ title }) => {
  return (
    <h2 className="block-title">{ title }</h2>
  );
}

export { Block, BlockTitle };