import { useMutation } from '@apollo/client';
import { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import { CourseItemList, CourseList } from '../../components/CourseList';
import ErrorPage from '../../components/ErrorPage';
import LoadingPage from '../../components/LoadingPage';
import { FETCH_FEED_NEW, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { BlockFeed, Course } from '../../types/Course';

import './styles.css';

const StartNew: React.FC<any> = ({ history, dispatch, user }) => {
  const [loading, setLoading] = useState(true);

  const [error, setError] = useState('');
  const [blocks, setBlocks] = useState([]);
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchFeedNew] = useMutation(FETCH_FEED_NEW);

  useEffect(() => {
    userVerify();
  }, []);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseFeed = await fetchFeedNew();
      const { feedNewMutation } = responseFeed.data;

      dispatch(setUser(account));
      setBlocks(feedNewMutation.blocks);

      setLoading(false);  
    } catch(error: any) {
      console.log(error);
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;

  console.log(blocks);

  return (
    <div className={`basic-screen-inner start`}>
      <BlockTitle title="ltimos Lanamentos" />
      { blocks.map((block: BlockFeed) => 
          <CourseList className="course-list-vertical">
            { block.courses.map((course: Course) => 
              <Block width="90%" className="course-vertical-block" onClick={() => history.push(`/curso/${course.id}`)}>
                <div className="course-item-list-image" style={{ backgroundImage: `url(${course.course_image})` }}></div>
                <div className="info">
                  <b>{course.name}</b>
                  <span>Prof. {course.teachers[0].name}</span>
                  <span>{ course.institute.name }</span>
                </div>
              </Block>) }
          </CourseList>
       ) }
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(StartNew);