import React from "react";
import './styles.css';

export interface InputProps {
  type: string,
  placeholder: string,
  Icon: any,
  required?: boolean,
  onChangeText?: (value: string) => void,
  value?: string
}

export interface TextareaProps {
  placeholder: string,
  required?: boolean,
  onChangeText?: (value: string) => void,
  value?: string,
  disabled?: boolean
}

export interface Option {
  name: string,
  value: string
}

export interface SelectProps {
  placeholder: string,
  Icon: any,
  options: Option[],
  required?: boolean,
  value?: string,
  onChangeText?: (value: string) => void
}

export interface ButtonProps {
  name: string,
  type?: any,
  disabled?: boolean,
  onClick?: () => void
}

export interface CheckboxTextProps {
  name?: string,
  children?: React.ReactNode
}

export interface CheckboxProps {
  name?: string,
  children?: React.ReactNode,
  checked?: boolean,
  className?: string,
  onChangeValue?: (checked: boolean) => void
}

export interface FormProps {
  children: React.ReactNode,
  className?: string,
  onSubmit?: (e: React.FormEvent<EventTarget>) => void
}

const Input: React.FC<InputProps> = ({ type, placeholder, Icon, required, value, onChangeText }) => {
  const onChange = (event: any) => {
    if(onChangeText) onChangeText(event.target.value);
  }

  return (
    <div className="form-input-container">
      <Icon size={25} />
      <input className="form-input" {...{type, placeholder, required, value, onChange}} />
    </div>
  );
}

const Textarea: React.FC<TextareaProps> = ({ placeholder, required, value, disabled, onChangeText }) => {
  const onChange = (event: any) => {
    if(onChangeText) onChangeText(event.target.value);
  }

  return (
    <div className="form-input-container">
      <textarea className="form-input" {...{placeholder, required, value, disabled, onChange}} />
    </div>
  );
}

const Select: React.FC<SelectProps> = ({ placeholder, Icon, required, options, value, onChangeText }) => {
  const onChange = (event: any) => {
    /* @ts-ignore */
    onChangeText(event.target.value);
  }

  return (
    <div className="form-input-container">
      <Icon size={25} />
      <select className="form-input form-select" required={required} value={value} onChange={onChange}>
        <option value="">{ placeholder }</option>
        { options.map((option: Option) => <option value={option.value}>{ option.name }</option>) }
      </select>
    </div>
  );
}

const Button: React.FC<ButtonProps> = ({ name, type, disabled, onClick }) => {
  type = type || 'button';
  return (
    <button className="form-button" type={type} onClick={onClick} disabled={disabled}>{ name }</button>
  );
}

const CheckboxText: React.FC<CheckboxTextProps> = ({ name, children }) => {
  return <span className="form-checkbox-text">{ name || children }</span>;
}

const Checkbox: React.FC<CheckboxProps> = ({ name, className, children, checked, onChangeValue }) => {
  if(children) {
    return (
      <div className={`form-checkbox-container ${className}`}>
        { /* @ts-ignore */ }
        <input type="checkbox" checked={checked} onChange={() => onChangeValue(!checked)} />
        { children }
      </div>
    );
  }

  return (
    <div className={`form-checkbox-container ${className}`}>
      { /* @ts-ignore */ }
      <input type="checkbox" checked={checked} onChange={() => onChangeValue(!checked)} />
      <CheckboxText name={name || ''} />
    </div>
  );
}

const RoundCheckbox: React.FC<CheckboxProps> = ({ name, className, children, checked, onChangeValue }) => {
  if(children) {
    return (
      <div className={`form-checkbox-container ${className}`}>
        <label className="container">
          { /* @ts-ignore */ }
          <input type="checkbox" checked={checked} onChange={() => onChangeValue(!checked)} />
          <span className="checkmark"></span>
        </label>
        { children }
      </div>
    );
  }

  return (
    <div className={`form-checkbox-container ${className}`}>
      <label className="container">
        { /* @ts-ignore */ }
        <input type="checkbox" checked={checked} onChange={() => onChangeValue(!checked)} />
        <span className="checkmark"></span>
      </label>
      <CheckboxText name={name || ''} />
    </div>
  );
}

const Form: React.FC<FormProps> = ({ children, className, onSubmit }) => {
  onSubmit = onSubmit || (() => {});
  return (
    <form className={`form ${className}`} onSubmit={onSubmit}>
      { children }
    </form>
  );
};

export { Input, Select, Button, CheckboxText, Checkbox, Form, RoundCheckbox, Textarea };

