import { useState } from "react";
import { BsClockFill } from "react-icons/bs";
import { MdArrowBack, MdArrowForward } from "react-icons/md";
import { Link, useHistory } from "react-router-dom";
import { Course } from "../../types/Course";

interface Props {
  title: string;
  courses: Course[];
  topicId?: number
}

const HorizontalCourseList: React.FC<Props> = ({ title, courses, topicId }) => {
  const [start, setStart] = useState(0);
  const [end, setEnd] = useState(2);
  
  const history = useHistory();

  if(!courses.length) return null;

  const checkNext = () => {
    if((end + 1) <= (courses.length - 1)) return true;
    return false;
  }

  const next = () => {
    if(!checkNext()) return;
    setStart(start + 3);
    setEnd(end + 3);
  }

  const back = () => {
    if(start - 3 < 0) {
      setStart(0);
      setEnd(2);
    } else {
      setStart(start - 3);
      setEnd(end - 3);
    }
  }

  return (
    <div className="course-list">
      <div className="course-list-title">
        <div className="course-list-title-info">
          <h3>{title}</h3>
          { topicId &&
          <Link to={`/topico/${topicId}`}>
            Ver todos os cursos
          </Link>
          }
        </div>
        <div className="course-list-title-arrows">
          <MdArrowBack size={24} onClick={back} style={{ color: !start ? 'rgba(57, 15, 174, 0.36)' : 'var(--blue)', cursor: !start ? 'default' : 'pointer' }} />
          <MdArrowForward size={24} onClick={next} style={{ color: !checkNext() ? 'rgba(57, 15, 174, 0.36)' : 'var(--blue)', cursor: !checkNext() ? 'default' : 'pointer'  }} />
        </div>
      </div>

      <div className="course-list-courses">
        {courses.slice(start, end + 1).map((course: Course) => (
          <div className="course-list-course" onClick={() => history.push(`/curso/${course.id}`)}>
            { /* @ts-ignore */ }
            <div className="course-list-course-image" style={{ 'background-image': `url(${course.course_image})` }}>
              <div className="course-list-course-image-university">
                <img src={course.institute.image} />
              </div>
              { course.percentageWatched && <div className="course-list-course-progress">{ course.percentageWatched.toFixed(0) }% Concludo!</div> }
            </div>
            <b>{ course.name }</b>
            <p>{ course.institute.name }</p>
            <div className="course-list-course-timer">
              <div>
                <BsClockFill />
                <span>{ course.workload } Horas de Contedo</span>
              </div>
            </div>
            { course.percentageWatched ? <button>Continuar</button> : <button>Comear</button> }
          </div>
        ))}

        { courses.slice(start, end + 1).length < 3 && <div style={{flex: 1}} /> }
        { courses.slice(start, end + 1).length < 2 && <div style={{flex: 1}} /> }
      </div>
    </div>
  );
};

export default HorizontalCourseList;
