import React, { useEffect, useRef, useState } from 'react';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import { Button, Checkbox, Form, Input, RoundCheckbox, Textarea } from '../../components/Form';
import Playlist from '../../components/Playlist';
import { ActivitiesBlock, Alternative, Content, Course, Activity as ActivityType } from '../../types/Course';

import { MdChevronLeft, MdChevronRight, MdDelete, MdInfo } from 'react-icons/md';

import './styles.css';
import { CREATE_ACTIVITY, DELETE_ACTIVITY, SUBMIT } from '../../graphql/mutations';
import { useMutation } from '@apollo/client';
import ErrorPage from '../../components/ErrorPage';
import { Modal } from '../../components/Modal';
import uploadImgurAPI from '../../utils/uploadImgurAPI';
import convertTextToHtml from './convertTextToHtml';

export interface ActivityProps {
  content: Content,
  course: Course | null,
  isAuthor: boolean,
  onContentClick: (content: Content) => void,
  onCertificateClick: () => void,
  onUserSubmit: (content: Content, activityId: number, userAnswerId: number) => void,
  onNewActivitySubmitted: (course: Course) => void,
  onDeleteActivity: (activitiesBlock: ActivitiesBlock) => void
}

export interface CreateActivityModalProps {
  activityBlock: ActivitiesBlock,
  onSubmitted: (course: Course) => void
  onExitClick: () => void
}

const CreateActivityModal: React.FC<CreateActivityModalProps> = ({ activityBlock, onSubmitted, onExitClick }) => {
  const [text, setText] = useState('');
  const [madeByMe, setMadeByMe] = useState(false);
  const [freeForUse, setFreeForUse] = useState(false);
  const [correctionLink, setCorrectionLink] = useState('');
  const [correctionText, setCorrectionText] = useState('');
  const [numberAlternatives, setNumberAlternatives] = useState('');
  const [alternativeTexts, setAlternativeTexts] = useState<string[]>([]);
  const [correctAlternativeNumber, setCorrectAlternativeNumber] = useState('');

  const [loading, setLoading] = useState(false);
  const [loadingImage, setLoadingImage] = useState(false);

  const [createActivitiesBlock] = useMutation(CREATE_ACTIVITY);

  const fileRef = useRef(null);

  const setAlternative = (t: string, index: number) => {
    const newAlternatives = [...alternativeTexts];
    newAlternatives[index] = t;
    setAlternativeTexts(newAlternatives);
  }

  const onSubmit = async (e: any) => {
    e.preventDefault();

    if(loading) return;

    try {
      setLoading(true);

      { /* @ts-ignore */ }
      const correct_alternative_text = alternativeTexts[correctAlternativeNumber - 1];

      const response = await createActivitiesBlock({ variables: { 
        text,
        made_by_me: madeByMe,
        free_for_use: freeForUse,
        correction_link: correctionLink,
        alternative_texts: alternativeTexts,
        correct_alternative_text,
        correction_text: correctionText,
        activities_block_id: Number(activityBlock.id)
      } });

      /* @ts-ignore */
      onSubmitted(response.data.createActivity);
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  const onSelectImageClick = () => {
    /* @ts-ignore */
    fileRef.current.click();
  }

  const onChangeFile = async (e: any) => {
    /* @ts-ignore */
    const image = e.target.files[0];
    
    if(loadingImage) return;

    try {
      setLoadingImage(true);

      const imageUploaded: any = await uploadImgurAPI(image);
      const course_image = imageUploaded.data.link;

      setText(`${text}\n[Imagem: ${course_image}]`);
      setLoadingImage(false);
    } catch(error: any) {
      setLoadingImage(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  const arrays = [];

  for(let i = 0; i < (numberAlternatives == '' ? 0 : Number(numberAlternatives)); i++)
    arrays.push(i);

  return (
  <Modal className="course-modal activity-modal" onExitClick={onExitClick}>
    <BlockTitle title="Criar Exerccio" />
    <Form onSubmit={onSubmit}>
      <input type="file" style={{ display: 'none' }} accept="image/png, image/gif, image/jpeg" ref={fileRef} onChange={onChangeFile} />
      <Textarea placeholder="Questo" onChangeText={setText} value={text} required={true} disabled={loadingImage} />
      <Input Icon={MdInfo} placeholder="Nmero de Alternativas" type="number" onChangeText={setNumberAlternatives} value={numberAlternatives} required={true} />
      { arrays.map((index: number) => <Input Icon={MdInfo} placeholder={`Alternativa ${index + 1}`} type="text" onChangeText={(text: string) => setAlternative(text, index)} value={alternativeTexts[index] ? alternativeTexts[index] : ''} required={true} />) }
      <Input Icon={MdInfo} placeholder="Nmero da Alternativa Correta" type="number" onChangeText={setCorrectAlternativeNumber} value={correctAlternativeNumber} required={true} />
      <Input Icon={MdInfo} placeholder="Link para Correo (Opcional)" type="text" onChangeText={setCorrectionLink} value={correctionLink} />
      <Textarea placeholder="Texto de Correo (Opcional)" onChangeText={setCorrectionText} value={correctionText} disabled={loadingImage} />
      <Checkbox checked={madeByMe} onChangeValue={setMadeByMe} name="Feito por Mim" />
      <Checkbox checked={freeForUse} onChangeValue={setFreeForUse} name="Grtis para Uso" />
      { loadingImage ? <Button name="Adicionando.." type="button" onClick={onSelectImageClick} /> : <Button name="Adicionar Imagem" type="button" onClick={onSelectImageClick} /> }
      { loading ? <Button name="Criando.." type="button" onClick={() => {}} /> : <Button type="submit" name="Criar" /> }
    </Form>
  </Modal>
  );
}

const Activity: React.FC<ActivityProps> = ({ content, course, isAuthor, onContentClick, onCertificateClick, onUserSubmit, onNewActivitySubmitted, onDeleteActivity }) => {
  const [isCreateActivityModalOpen, setIsCreateActivityModalOpen] = useState(false);
  const [deletingActivity, setDeletingActivity] = useState(false);

  const exercisesNumber = content.activitiesblock?.exercises_number;

  const [activityNumber, setActivityNumber] = useState(1);
  const [alternativeSelected, setAlternativeSelected] = useState(exercisesNumber ? content.activitiesblock?.activities[0].user_answer_id || 0 : 0);
  /* @ts-ignore */
  const [correctAnswer, setCorrectAnswer] = useState<number | null>(exercisesNumber ? content.activitiesblock?.activities[0].user_answer_id ? content.activitiesblock?.activities[0].correct_alternative_id : null : null);
  const [error, setError] = useState('');
  const [sending, setSending] = useState(false);

  const [submit] = useMutation(SUBMIT);
  const [deleteActivity] = useMutation(DELETE_ACTIVITY);

  useEffect(() => {
    setActivityNumber(1);
  }, [content.activitiesblock?.id, content.activitiesblock?.exercises_number]);

  const onSubmit = async () => {
    if(!alternativeSelected) return;

    setSending(true);

    const activity_id = Number(content.activitiesblock?.activities[activityNumber - 1].id);

    try {
      const submitResponse = await submit({ variables: {
        activity_id,
        alternative_id: Number(alternativeSelected),
      } });
      setCorrectAnswer(submitResponse.data.submit.correctAlternative.id);
      setSending(false);
      onUserSubmit(content, activity_id, alternativeSelected);
    } catch(error) {
      console.log(error);
      setSending(false);
      setError('Erro ao submetar resposta.');
    }
  }

  const chevronLeftClick = () => {
    setAlternativeSelected(content.activitiesblock?.activities[activityNumber - 2].user_answer_id || 0);
    /* @ts-ignore */
    setCorrectAnswer(content.activitiesblock?.activities[activityNumber - 2].user_answer_id ? content.activitiesblock?.activities[activityNumber - 2].correct_alternative_id : null);
    setActivityNumber(activityNumber-1);
  }

  const chevronRightClick = () => {
    setAlternativeSelected(content.activitiesblock?.activities[activityNumber].user_answer_id || 0);
    /* @ts-ignore */
    setCorrectAnswer(content.activitiesblock?.activities[activityNumber].user_answer_id ? content.activitiesblock?.activities[activityNumber].correct_alternative_id : null);
    setActivityNumber(activityNumber+1);
  }

  const openInNewTab = (url: string) => {
    /* @ts-ignore */
    window.open(url, '_blank').focus();
   }

  const activity = content.activitiesblock?.activities[activityNumber - 1];

  const onSubmitted = (course: Course) => {
    setIsCreateActivityModalOpen(false);
    onNewActivitySubmitted(course);
  }

  const onClickDeleteActivity = async (activity: ActivityType) => {
    if(deletingActivity) return;

    try {
      setDeletingActivity(true);

      const response = await deleteActivity({ variables: { id: Number(activity.id) } });
      /* @ts-ignore */
      onDeleteActivity(response.data.deleteActivity);

      setDeletingActivity(false);
    } catch(error: any) {
      setDeletingActivity(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  if(error) return <ErrorPage text={error} />;

  if(!exercisesNumber) {
    return (
    <BasicScreenInner className="activity">
      <Block className="activity-block">
        <BlockTitle title="Sem Exerccios" />
        { isAuthor ? <Button name="Adicionar Exerccio" onClick={() => setIsCreateActivityModalOpen(true)} /> : null }
      </Block>
      <Playlist className="video-playlist" blocks={course?.blocks} onContentClick={onContentClick} onCertificateClick={onCertificateClick} />
      { /* @ts-ignore */ }
      { isCreateActivityModalOpen ? <CreateActivityModal activityBlock={content.activitiesblock} onExitClick={() => setIsCreateActivityModalOpen(false)} onSubmitted={onSubmitted} /> : null }
    </BasicScreenInner>
    );
  }

  return (
    <BasicScreenInner className="activity">
      <Block className="activity-block">
        { /* @ts-ignore */ }
        { isAuthor ? <MdDelete size="22" className="course-video-finished-icon" onClick={() => onClickDeleteActivity(content.activitiesblock?.activities[activityNumber - 1])} /> : null }
        { isAuthor ? <Button name="Adicionar Exerccio" onClick={() => setIsCreateActivityModalOpen(true)} /> : null }
        <br />
        <div className="activity-block-header">
          { activityNumber != 1 ? <MdChevronLeft size={25} onClick={chevronLeftClick} /> : null }
          <BlockTitle title={`Atividade ${activityNumber} de ${content.activitiesblock?.exercises_number}`} />
          { activityNumber != content.activitiesblock?.exercises_number ? <MdChevronRight size={25} onClick={chevronRightClick} /> : null }
        </div>
        
        { /* @ts-ignore */ }
        <div className="activity-text" dangerouslySetInnerHTML={{__html: content.activitiesblock?.activities[activityNumber - 1] ? convertTextToHtml(content.activitiesblock?.activities[activityNumber - 1].text) : ''}} />

        <hr />

        <div className="activity-alternatives">
          { content.activitiesblock?.activities[activityNumber - 1] ? content.activitiesblock?.activities[activityNumber - 1].alternatives.map((alternative: Alternative) => {
            if(correctAnswer) {
              if(correctAnswer != alternative.id && alternativeSelected == alternative.id)
                return <RoundCheckbox className="alternative-wrong" key={`alter${alternative.id}`} name={alternative.value} checked={alternativeSelected == alternative.id} onChangeValue={() => {}} />;
              else if(correctAnswer == alternative.id)
                return <RoundCheckbox className="alternative-corrected" key={`alter${alternative.id}`} name={alternative.value} checked={alternativeSelected == alternative.id} onChangeValue={() => {}} />;
              else
                return <RoundCheckbox key={`alter${alternative.id}`} name={alternative.value} checked={alternativeSelected == alternative.id} onChangeValue={() => {}} />;
            } else {
              return <RoundCheckbox key={`alter${alternative.id}`} name={alternative.value} checked={alternativeSelected == alternative.id} onChangeValue={() => setAlternativeSelected(alternative.id)} />;
            }
            }) : null }
          { !sending && !correctAnswer ? <Button name="Submeter" onClick={onSubmit} /> : null }
          { sending && !correctAnswer ? <Button name="Enviando.." /> : null }
          { correctAnswer ? <Button name="Enviado" /> : null }
        </div>

        { correctAnswer && (activity?.correction_link || activity?.correction_text) ? <hr /> : null}
        { correctAnswer && activity?.correction_text ?  <BlockTitle title="Resoluo" /> : null}
        { correctAnswer && activity?.correction_text ?  <div className="activity-text" dangerouslySetInnerHTML={{__html: convertTextToHtml(activity?.correction_text)}} /> : null}
        { correctAnswer && activity?.correction_link ? <Button name="Visualizar Resoluo" onClick={() => openInNewTab(activity?.correction_link || "")} /> : null}
      </Block>
      <Playlist className="video-playlist" blocks={course?.blocks} onContentClick={onContentClick} onCertificateClick={onCertificateClick} />
      { /* @ts-ignore */ }
      { isCreateActivityModalOpen ? <CreateActivityModal activityBlock={content.activitiesblock} onExitClick={() => setIsCreateActivityModalOpen(false)} onSubmitted={onSubmitted} /> : null }
      { deletingActivity ? <Modal><BlockTitle title="Deletando..." /></Modal> : null }
    </BasicScreenInner>
  );
}

export default Activity;