import { gql } from '@apollo/client';

const LOGIN = gql`
  mutation login(
    $email: String!
    $password: String!
  ) {
    login(
      data: { 
        email: $email
        password: $password
      }
    ) {
      user {
        id
        name
        email
        photo_url
        teacher_verified
        selected_interest
      }
      token
    }
  }
`;

const REGISTER = gql`
  mutation createUser(
    $name: String!
    $email: String!
    $password: String!
    $city_id: Float!
    $receive_updates: Boolean!
    $user_type: String!
  ) {
    createUser(
      data: {
        name: $name
        email: $email
        password: $password
        city_id: $city_id
        receive_updates: $receive_updates
        user_type: $user_type
      }
    ) {
      id
      name
      email
      photo_url
      teacher_verified
      selected_interest
    }
  }
`;

const UPDATE_USER = gql`
mutation updateUser(
  $cpf: String
) {
  updateUser(
    data: {
      cpf: $cpf
    }
  ) {
    id
  }
}
`; 

const WATCH_LESSON = gql`
mutation watchLesson(
  $lesson_id: Float!
  $course_id: Float!
) {
  watchLesson(data: {
    lesson_id: $lesson_id
    course_id: $course_id
  })
}
`;

const WATCH = gql`
mutation watch(
  $id: Float!
) {
  watch(id: $id)
}
`;

const SUBMIT = gql`
  mutation submit(
    $activity_id: Float!
    $alternative_id: Float!
  ) {
    submit(data: {
      activity_id: $activity_id
      alternative_id: $alternative_id
    }) {
      correctAlternative {
        id
        value
      }
      isCorrect
    }
  }
`;

const ADD_INTEREST_TOPICS = gql`
mutation addInterestTopics(
  $topicsSelected: [Float!]!
) {
  addInterestTopics(data: {
    topic_ids: $topicsSelected
  })
}
`;

const CREATE_COURSE = gql`
mutation createCourse(
  $name: String!
  $course_image: String!
  $copy: String!
  $description: String!
  $requirements: String!
  $institute_id: Float!
  $workload: Float!
  $topic_id: Float!
  $subtopic_id: Float!
) {
  createCourse(data: {
    name: $name
    course_image: $course_image
    copy: $copy
    description: $description
    requirements: $requirements
    institute_id: $institute_id
    workload: $workload
    topic_id: $topic_id
    subtopic_id: $subtopic_id
  }) {
    id
    name
  }
}
`;

const UPDATE_COURSE = gql`
mutation updateCourse(
  $id: Float!
  $name: String
  $course_image: String
  $published: Boolean
) {
  updateCourse(
    id: $id
    data: {
    name: $name
    course_image: $course_image
    published: $published
  }) {
    id
    name
    course_image
    published
    blocks {
      id
      name
      position
      contents {
        id
        name
        position
        watched
        lesson {
          id
          description
          youtube_link
          attachments {
            id
            name
            link
          }
        }
        activitiesblock {
          id
          exercises_number
          activities {
            id
            text
            made_by_me
            free_for_use
            correct_alternative_id
            correction_link
            correction_text
            correction_youtube_link
            user_answer_id
            alternatives {
              id
              value
            }
          }
        }
      }
    }
    copy
    institute {
      id
      name
      image
      white_image
      description
    }
    teachers {
      id
      name
      photo_url
    }
  }
}
`;



