import { useMutation } from '@apollo/client';
import React, { useState } from 'react';
// @ts-ignore
import ReactPlayer from 'react-player/youtube';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import { Button } from '../../components/Form';
import Playlist from '../../components/Playlist';
import { WATCH_LESSON } from '../../graphql/mutations';
import { Content, Course } from '../../types/Course';
import youtubeParser from '../../utils/youtubeParser';

import './styles.css';

export interface VideoProps {
  content: Content,
  course: Course | null,
  onContentClick: (content: Content) => void,
  onWatchChange: (watched: boolean) => void,
  onCertificateClick: () => void
}

const Video: React.FC<VideoProps> = ({ content, course, onContentClick, onCertificateClick, onWatchChange }) => {
  const [watchLesson] = useMutation(WATCH_LESSON);
  const [progress, setProgress] = useState(0);

  const onWatchClick = async () => {
    setProgress(0);
    try {
      if(!content.watched)
        watchLesson({ variables: { lesson_id: Number(content.lesson?.id), course_id: Number(course?.id) } });
      
      onWatchChange(true);

      let nextContent;
      let isContent = false;

      course?.blocks.forEach((block) => {
        block.contents.forEach((nContent) => {
          if(isContent && !nContent.watched) {
            nextContent = nContent;
            isContent = false;
          }
          if(isContent) return;
          if(nContent.id === content.id) isContent = true;
        })
      })
      
      if(isContent) {
        onCertificateClick();
        return;
      }
      
      if(nextContent)
        onContentClick(nextContent);
    } catch(error) {
      console.log(error);
    }
  }

  /* @ts-ignore */
  const youtubeLink: string = content.lesson?.youtube_link;

  return (
    <BasicScreenInner className="video">
      <Block className="video-block">
      
        <div className="video-iframe-container">
          { /* @ts-ignore */ }
          <ReactPlayer url={`${youtubeLink}`} onProgress={a => setProgress(a.played * 100)} controls={true} playing={true} />
        </div>
        <div className="video-block-info">
          <BlockTitle title={content.name} />
          <p>
            { content.lesson?.description }
          </p>
          <div className="video-block-title">
            <Button name='Continuar' disabled={progress < 30} onClick={onWatchClick} />
          </div>
        </div>
      </Block>
      <Playlist className="video-playlist" blocks={course?.blocks} onContentClick={onContentClick} onCertificateClick={onCertificateClick} />
      
    </BasicScreenInner>
  );
}

export default Video;